<template>
    <div class="dataManage-navbtn-wrapper">
        <router-link :to="edit" tag="button" class="el-button el-button--mini" active-class="el-button--primary" >修改</router-link>
        <el-button class="el-button el-button--mini" @click="deleteSupplier">删除</el-button>
    </div>
</template>
<script>
    export default {
        name:"TopNavBtn",
        data(){
            return{
            }
        },
        methods:{
          deleteSupplier(){
              this.$confirm('确认删除该项?', '提示', {
                  confirmButtonText: '确定',
                  cancelButtonText: '取消',
                  type: 'warning'
              }).then(() => {
                      this.$http.post("/ys-web-wms/supplier/delete", {id:this.$route.params.id})
                          .then((response) => {
                              if (response.data.data.status == '200') {
                                  this.$message({
                                      'type': 'success',
                                      message: "操作成功",
                                      'showClose': true
                                  });
                                      this.$store.commit('IS_REFRESH')
                              } else {
                                  this.$message({
                                      'type': 'error',
                                      message: "操作失败，,请重试或联系管理",
                                      'showClose': true
                                  });
                              }
                          })
                          .catch((error) => {
                              console.log(error)
                          });
              }).catch(() => {
                  this.$message({
                      type: 'info',
                      message: '已取消删除'
                  });
              });
          }
        },
        computed:{
            "edit":function(){
                return "/dataManagement/supplier/detail/"+this.$route.params.id+"/edit";
            },
        },
    }
</script>
<style>

</style>



