<template>
    <div>
        <p class="dataManagement-title">配置项明细</p>
        <br/>
        <el-table :data="list" border>
            <el-table-column  label="配置项名称"  align="center" :show-overflow-tooltip="true" prop="name">
            </el-table-column>
            <el-table-column  label="配置项值"  align="center" :show-overflow-tooltip="true" prop="value">
                <template slot-scope="sp">
                    <span v-if="sp.row.type==YesNo[0].value.key">{{Number(sp.row.value)|enumtext(YesNo)}}</span>
                    <span v-else>{{sp.row.value}}</span>
                </template>
            </el-table-column>
            <el-table-column  label="创建日期"  align="center" :show-overflow-tooltip="true" prop="createDate">
            </el-table-column>
            <el-table-column  label="配置说明"  align="center" :show-overflow-tooltip="true" prop="desc">
            </el-table-column>
            <el-table-column  label="操作"  align="center" :show-overflow-tooltip="true">
                <template slot-scope="sp">
                    <el-button type="primary" icon="el-icon-edit" size="mini" @click="edit(sp.row)"></el-button>
                    <el-button type="danger" icon="el-icon-delete" size="mini" @click="delConfig(sp.row)"></el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>
<script>
    export default{
        name:"ConfigList",
        props:{
            searchParams:{
                type:Object
            },
            searchMark:{
                type:Boolean,
                default:true,
            }
        },
        mounted(){
          this.doAjax();
        },
        data(){
            return{
                list:[],
            }
        },
        methods:{
            edit(val){
                this.$router.push("/dataManagement/config/edit/"+val.recordId)
            },
            delConfig(val){
                this.$confirm('确认删除该项?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$http.post('/ys-web-wms/config/delConfig',{id:val.recordId}).then(
                        (res)=>{
                            if(res.data.data.status==200){
                                this.$message({
                                    'type': 'success',
                                    message: "操作成功",
                                    'showClose': true
                                });
                                this.doAjax()
                            }else{
                                this.$message({
                                    'type': 'warning',
                                    message: "操作失败",
                                    'showClose': true
                                });
                            }
                        }
                    ).catch((e)=>{
                        console.log(e.message)
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            doAjax(){
                this.$http.post('/ys-web-wms/config/getList',{data:JSON.stringify(this.searchParams)}).then(
                    (res)=>{
                        this.list=res.data.data?res.data.data:[]
                    }
                )
            }
        },
        computed:{
            YesNo(){
                return this.$store.state.enumList.YesNo
            }
        },
        watch:{
            'searchMark'(){
                this.doAjax()
            }
        }
    }
</script>


