<template>
    <keep-alive>
        <component :is="com"></component>
    </keep-alive>
</template>
<script>
    import ConfigList from './ConfigList.vue'
    import ConfigEdit from './ConfigEdit.vue'
    export default{
        name:'ConfigDetail',
        data(){
            return{
                com:'ConfigList'
            }
        },
        watch:{
            '$route.query.mode'(){
                this.com = this.$route.query.mode
            }
        },
        components:{
            ConfigEdit,
            ConfigList
        }
    }
</script>


