<template>
  <div class="card-box">
    <div class="card-box-inner">
      <div class="searchBar-main">
        <el-input v-model="searchParams.vendorsName" placeholder="供应商名称" size="mini" style="width: 32%">
        </el-input>
        <el-cascader  :options="materialTree" style="width:32%" :props="defaultProps"
                     :show-all-levels="false" size="mini" v-model="materialType" placeholder="物料类别" clearable></el-cascader>
        <el-input clearable v-model="searchParams.location"  placeholder="所在地" size="mini" style="width:32%">
        </el-input>
        <el-input clearable v-model="searchParams.contactsName"  placeholder="联系人" size="mini" style="width:32%">
        </el-input>
        <el-input clearable v-model="searchParams.companyTelphone"  placeholder="公司电话" size="mini" style="width:32%">
        </el-input>
        <el-select clearable v-model="credit"  placeholder="信誉" size="mini" style="width:32%">
          <el-option value="1" label="0-20"></el-option>
          <el-option value="2" label="20-40"></el-option>
          <el-option value="3" label="40-60"></el-option>
          <el-option value="4" label="60-80"></el-option>
          <el-option value="5" label="80-100"></el-option>
        </el-select>
        <div style="text-align:right;padding:5px 10px 0 0 ">
          <el-button type="primary" @click="search"  size="mini">搜索</el-button>
          <el-button type="success" @click="supplierAdd"  size="mini">新增供应商</el-button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
    export default {
        name:"SupplierSearchBar",
        props:{
            params:Object
        },
        mounted(){

        },
        data(){
            return {
                searchParams: Object.assign({},this.params),
                materialType:[],
                credit:'',
                defaultProps:{
                    value:'name',
                    label:'name',
                    children:'childList'
                }
            }
        },
        methods:{
            handleChange(value) {
                console.log(value);
            },
            search(){
                if(this.materialType){
                    this.searchParams.materialMainCategoryName = this.materialType[this.materialType.length-1]
                }
                if(this.credit){
                this.searchParams.creditDegreeStart = (this.credit-1)*20
                this.searchParams.creditDegreeEnd = this.credit*20
                }else{
                    this.searchParams.creditDegreeStart = 0
                    this.searchParams.creditDegreeEnd = 100
                }
                this.$emit("setParams",this.searchParams);
            },
            supplierAdd(){
                this.$router.push("/dataManagement/supplier/add")
            }
        },
        computed:{
            materialTree(){
                return this.$store.state.selectList.materialCategoryTree
            },
        },

    }
</script>
<style>

</style>



