<template>
    <div class="dataManagement-body">
        <el-tabs v-model="activeName" type="border-card" @tab-click="handleClick">
            <el-tab-pane label="仓库管理" name="store"></el-tab-pane>
            <el-tab-pane label="供应商管理" name="supplier"></el-tab-pane>
            <el-tab-pane label="配置项管理" name="config"></el-tab-pane>
            <el-tab-pane label="字段显示设置" name="field"></el-tab-pane>
            <template name="fade">
                <router-view></router-view>
            </template>
        </el-tabs>
    </div>
</template>
<script>
    export default {
        name:'DMContainer',
        data() {
            return {
                activeName:"store",
            }
        },
        methods: {
            handleClick(tab, event) {
                let name = this.$route.path.split('/')[2]
                if( name != this.activeName)
                    this.$router.push("/dataManagement/"+tab.name);
            },
        },
        watch:{
            "$route":function () {
                this.activeName = this.$route.path.split('/')[2];
            },
        }
    }
</script>
<style>
    .dataManagement-body{
        padding:5px;
        min-height:500px;
    }
    .dataManagement-body-container{
        background:#F9FAFC;
    }
    .search-div{
        border-bottom:1px solid;
        border-color:rgba(228, 228, 228, 1);
    }
    .search-div-inner{
        padding-bottom:5px;
    }
    .dataManage-navbtn-wrapper{
        width: 100%;
        text-align: right;
        border-bottom:1px solid #D3DCE6;
        margin-bottom: 20px;
        padding-bottom:7px;
    }
    .dataManagement-title{
        font-size:14px;
        color:#2B7C9F;
        font-weight: bold;
         text-indent:1em;
    }
    .dataManagement-table{
        font-size:12px;
        table-layout:fixed;
    }
    .dataManagement-table td{
        color:#000!important;
    }
    .materiel-popover{
        max-height:200px;
        overflow:auto
    }
    .supplier-body{
        border: 1px solid #D3DCE6;
        border-radius: 4px;
        background-color: #fff;
        overflow: hidden;
        margin:5px;
    }
    .dataManagement-table td .el-form-item__error{
        display:none!important;
    }
    .dataManagement-body td .el-form-item{
        margin-bottom:0px!important;
    }
    .store-tree{
        border: 1px solid #d1dbe5;
    }
    .dataManage-navbtn-wrapper .el-dialog__header{padding: 0px}
    .dataManage-navbtn-wrapper .el-dialog__body{padding: 0px}
    .dataManage-navbtn-wrapper .el-dialog{width: 335px}
    .dataManage-navbtn-wrapper .el-dialog__close{display: none}
</style>


