<template>
  <div>
    <div class="right-content-body-topTitle not-print">
      打印条码
    </div>
    <div style="padding: 0px 5px;" id="allocationPrint" class="printTag-waterfall">
      <div v-for="item in printList" id="allocation" style="width: 323px;height: 178px;margin-top: 17px" class="printTag-tableBox">
        <barcode :value="item.code.replace(/-/g,'')" :options="{ displayValue: false }"></barcode>
        <div style="text-align: center">{{item.code.replace(/-/g,'')}}</div>
      </div>
    </div>
    <div style="text-align: right;padding: 10px">
      <el-button @click="print" size="mini" type="primary" class="not-print">打印</el-button>
      <el-button @click="cancel" size="mini" class="not-print">取消</el-button>
    </div>
  </div>
</template>
<script>
  import html2Canvas from 'html2canvas'
  import JsPDF from 'jspdf'
  import VueBarcode from '@xkeshi/vue-barcode'
  export default{
    name:"PrintAllocation",
      props:["printList"],
    mounted(){

    },
    data(){
      return{
        isShow:false,
        pdfData:""
      }
    },
    methods:{
      print(){
        let _this = this;
        let tagHeight = document.getElementById("allocation").clientHeight+17;
        let tagWidth = document.getElementById("allocation").clientWidth+10;
        let position = -10;
        this.getPdf("#allocationPrint","条码打印",position,tagWidth,tagHeight,function (doc,blob,pageData) {
          var url = window.URL.createObjectURL(blob)
//          var arr = pageData.split(','), mime = arr[0].match(/:(.*?);/)[1],
//            bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n);
//          while(n--){
//            u8arr[n] = bstr.charCodeAt(n);
//          }
////          return new Blob([u8arr], {type:mime});
//          var url = window.URL.createObjectURL(new Blob([u8arr], {type:mime}))
          var win=window.open(url);
          win.print();
          win.location.reload();
        });
      },
      cancel(){
        this.$emit("close",true)
      }
    },
    components:{
        "barcode":VueBarcode,
    }
  }
</script>
<style>
  /*瀑布流层*/
  .printTag-waterfall{
    -moz-column-count:1; /* Firefox */
    -webkit-column-count:1; /* Safari 和 Chrome */
    column-count:1;
  }
  /*一个内容层*/
  .printTag-tableBox{
    /*padding: 12px 2% 0 2%;*/
    /*padding-left: 3%;*/
    -moz-page-break-inside: avoid;
    -webkit-column-break-inside: avoid;
    break-inside: avoid;
  }
</style>



