<template>
    <div>
        <p class="dataManagement-title">{{isAdd?'新增配置项':'修改配置项'}}</p>
        <br/>
        <el-form :model="form" ref="configForm" :rules="rules" label-width="180px" inline-message>
                <el-row>
                    <el-col :span="12"><el-form-item label="配置项名称：" prop="name"><el-input size="small"  v-model="form.name" style="width: 60%"></el-input></el-form-item></el-col>
                    <el-col :span="12"><el-form-item label="配置项类别："><el-switch
                        v-model="form.type"
                        :active-text="configItemType[0].name"
                        :inactive-text="configItemType[1].name"
                        :active-value="configItemType[0].code"
                        :inactive-value="configItemType[1].code"
                            @change="changeType">
                    </el-switch></el-form-item></el-col>
                </el-row>
            <el-row>
            <el-col :span="12">
                <el-form-item label="配置项值：" prop="value"><el-switch
                        v-model="form.value"
                        :active-text="YesNo[0].name"
                        :inactive-text="YesNo[1].name"
                        :active-value="YesNo[0].code"
                        :inactive-value="YesNo[1].code"
                        v-if="form.type == YesNo[0].code"
                        >
                </el-switch>
                    <el-input size="small" v-else v-model="form.value" style="width: 60%"></el-input></el-form-item>
            </el-col>
            </el-row>
               <el-form-item label="配置项说明："><el-input size="small" v-model="form.desc" style="width: 90%"></el-input></el-form-item>
        </el-form>
        <div style="text-align:center;margin-top:10px">
            <el-button type="primary" size="mini" @click="submit" :disabled="uploading">保存</el-button>
            <el-button @click="goBack" size="mini">取消</el-button>
        </div>
    </div>
</template>
<script>
    export default {
        name:"Edit",
        mounted(){
          if(!this.isAdd) {
              this.doAjax();
          }
        },
        data(){
            return{
                form:{
                    name:'',
                    value:'',
                    desc:'',
                    type:''
                },
                rules:{
                    name:[{required:true,message:'请输入配置项名称',trigger:'blur'}],
                    value:[{required:true,message:'请输入配置项值',trigger:'blur'}]
                },
                uploading:false
            }
        },
        methods:{
            goBack(){
                this.$router.push("/dataManagement/config/list")
            },
            changeType(){
              this.form.value=this.form.oldValue
                this.$refs['configForm'].clearValidate()
            },
            doAjax(){
                this.$http.post('/ys-web-wms/config/getInfo',{id:this.$route.params.id}).then(
                    (res)=>{
                        if(res.data)
                            this.form=Object.assign({  name:'',
                                value:'',
                                desc:'',
                                type:''},res.data.data)
                        this.form.desc = res.data.data.introduction
                        this.form.oldValue = res.data.data.value
                    }
                )
            },
            submit(){
                let url = "editConfig"
                if(this.isAdd){
                    url = "addConfig"
                }
                this.$refs['configForm'].validate((valid) => {
                    if (valid) {
                        this.uploading = true
                        this.$http.post("/ys-web-wms/config/"+url, {params: JSON.stringify(this.form),id:this.$route.params.id})
                            .then((response) => {
                                if (response.data.data.status == '200') {
                                    this.$message({
                                        'type': 'success',
                                        message: "操作成功",
                                        'showClose': true
                                    });
                                    this.$router.push('/dataManagement/config/list')
                                } else {
                                    this.$message({
                                        'type': 'error',
                                        message: "操作失败，,请重试或联系管理",
                                        'showClose': true
                                    });
                                    this.uploading = false
                                }
                            })
                            .catch((error) => {
                                console.log(error)
                                this.uploading = false
                            });
                    }else{
                        this.$nextTick(()=> {
                            document.getElementsByClassName("el-form-item__error")[0].parentNode.getElementsByClassName('el-input__inner')[0].scrollIntoView()
                            document.getElementsByClassName("el-form-item__error")[0].parentNode.getElementsByClassName('el-input__inner')[0].focus()
                        })
                    }
                })
            }
        },
        computed:{
            isAdd(){
                return this.$route.name == 'configAdd'
            },
            YesNo(){
                return this.$store.state.enumList.YesNo
            },
            configItemType(){
                return this.$store.state.enumList.configItemType
            }
        },
        components:{
        }
    }
</script>
<style scoped>

</style>



