<template>
    <div class="dataManagement-body-container">
        <el-row >
            <el-col id="leftMainBox" :span="8">
                <div class="dataManagement-body supplier-body">
                    <supplier-search-bar :params="searchParams" @setParams="setParams"></supplier-search-bar>
                    <br/>
                    <el-table :data="tableData"  border :row-class-name="highlightCurrent" @row-click="rowCurrentChange" style="width: 100%" >
                        <el-table-column label="物料类别" prop="materialMainCategoryName" width="127" align="center" :show-overflow-tooltip="true"></el-table-column>
                        <el-table-column label="公司名称" prop="companyName"  align="center" :show-overflow-tooltip="true"></el-table-column>
                        <el-table-column label="所在地" prop="location" width="100"  align="center" :show-overflow-tooltip="true"></el-table-column>
                        <el-table-column label="信誉" prop="creditDegree"  align="center" width="80">
                        </el-table-column>
                    </el-table>
                    <div class="pageBox" style="margin: 30px auto">
                        <el-pagination
                                @current-change="handleCurrentChange"
                                :current-page="currentPage"
                                :page-size="pageSize"
                                background
                                small
                                layout="prev, pager, next"
                                :total="count">
                        </el-pagination>
                    </div>
                </div>
            </el-col>
            <el-col id='rightMainBox' :span="16" >
                <div class="dataManagement-body supplier-body">
                    <transition name="fade">
                        <router-view></router-view>
                    </transition>
                </div>
            </el-col>
        </el-row>
    </div>
</template>
<script>
    import SupplierSearchBar from './SupplierSearchBar.vue'
    export default {
        name:'SupplierManagement',
        components:{
            SupplierSearchBar
        },
        mounted(){
          this.doAjax()
        },
        data(){
            return{
                tableData:[],
                currentPage:1,
                count:0,
                pageSize:10,
                currentRow: {
                    id:0,
                },
                searchParams:{
                    vendorsName:'',
                    materialMainCategoryName:'',
                    location:'',
                    contactsName:'',
                    companyTelphone:'',
                    creditDegreeStart:0,
                    creditDegreeEnd:100,
                }
            }
        },
        methods:{
            handleCurrentChange(val) {
                this.currentPage = val;
                this.doAjax()
            },
            highlightCurrent(val){
                return (val.row.recordId == this.currentRow.recordId ? 'info-row' :'');
            },
            rowCurrentChange(val){
                this.currentRow = val
                this.$router.push('/dataManagement/supplier/detail/'+val.recordId+'/info')
            },
            doAjax(){
                this.$http.post('/ys-web-wms/supplier/getList',{data:JSON.stringify(this.searchParams),page:this.currentPage}).then(
                    (res)=>{
                        if(res.data)
                            this.tableData=res.data.data?res.data.data.list:[]
                        this.count = res.data.data?res.data.data.count:0
                        this.pageSize = res.data.data?res.data.data.pageSize:10
                        if(this.tableData){
                            let firstrow = this.tableData[0]
                            this.rowCurrentChange(firstrow)
                        }
                    }
                )
            },
            setParams(val){
                this.searchParams = val
                this.doAjax()
            }
        },
        computed:{
            currentInfo(){
                return this.$store.state.moduleDM.currentRow
            },
            is_refresh(){
                return this.$store.state.moduleDM.is_refresh
            }
        },
        watch:{
            'currentInfo'(){
                this.currentRow.companyName =  this.currentInfo.companyName
                this.currentRow.creditDegree =  this.currentInfo.creditDegree
                this.currentRow.location =  this.currentInfo.location
            },
            'is_refresh'(){
                this.doAjax()
            }
        }
    }
</script>
<style>

</style>


