<template>
    <div>
        <p class="dataManagement-title">货柜基本信息</p>
        <br/>
        <table class="info-table dataManagement-table">
            <tbody>
            <tr>
                <td>货柜编码：</td>
                <td>{{info.shelfCode}}</td>
                <td>货柜名称：</td>
                <td>{{info.shelfName}}</td>
            </tr>
            <tr>
                <td>货柜类型：</td>
                <td>{{info.shelfType}}</td>
                <td>物料类别：</td>
                <td>{{info.materialCategoryName?info.materialCategoryName.join(","):''}}</td>
            </tr>
            <tr>
                <td>创建日期：</td>
                <td>{{info.createDate}}</td>
                <td></td>
                <td></td>
            </tr>
            </tbody>
        </table>
    </div>
</template>
<script>
    export default {
        name:"ConInfo",
        mounted(){
            this.doAjax();
        },
        data(){
            return{
                info:{}
            }
        },
        methods:{
            doAjax(){
                this.$http.post('/ys-web-wms/store/getContainerInfo',{id:this.$route.params.id}).then(
                    (res)=>{
                            this.info=Object.assign({},res.data.data)
                        this.$store.commit('SET_CURRENTROW',this.info)
                    }
                ).catch((e)=>{
                    this.info={}
                })
            }
        },
        watch:{
            '$route.params.id'(){
                this.doAjax()
            }
        }
    }
</script>
<style scoped>

</style>



