<template>
    <div>
        <p class="dataManagement-title">库区基本信息</p>
        <br/>
        <table class="info-table dataManagement-table">
            <tbody>
            <tr>
                <td>库区编码：</td>
                <td>{{info.storeAreaCode}}</td>
                <td>库区名称：</td>
                <td>{{info.storeAreaName}}</td>
            </tr>
            <tr>
                <td>库区类型：</td>
                <td>{{info.storeAreaType}}</td>
                <td>物料类别：</td>
                <td>{{info.materialCategoryNames?info.materialCategoryNames.join(","):''}}</td>
            </tr>
            <tr>
                <td>创建日期：</td>
                <td>{{info.createDate}}</td>
                <td></td>
                <td></td>
            </tr>
            </tbody>
        </table>
    </div>
</template>
<script>
    export default {
        name:"AreaInfo",
        mounted(){
            this.doAjax();
        },
        data(){
            return{
                info:{}
            }
        },
        methods:{
            doAjax(){
                this.$http.post('/ys-web-wms/store/getAreaInfo',{id:this.$route.params.id}).then(
                    (res)=>{
                            this.info=Object.assign({},res.data.data)
                        this.$store.commit('SET_CURRENTROW',this.info)
                    }
                ).catch((e)=>{
                    this.info={}
                })
            }
        },
        watch:{
            '$route.params.id'(){
                this.doAjax()
            }
        }
    }
</script>
<style scoped>

</style>



