<template>
    <div>
        <p class="dataManagement-title">基本信息</p>
        <br/>
        <table class="info-table dataManagement-table">
            <tbody>
            <tr>
                <td>物料大类：</td>
                <td>{{info.materialTypeName}}</td>
                <td>公司名称：</td>
                <td>{{info.companyName}}</td>
                <td>编号：</td>
                <td>{{info.companyCode}}</td>
            </tr>
            <tr>
                <td>固定供应商：</td>
                <td>{{info.isFixedVendors|enumtext(YesNo)}}</td>
                <td>省份：</td>
                <td>{{info.province}}</td>
                <td>所在地：</td>
                <td>{{info.location}}</td>
            </tr>
            <tr>
                <td>企业类型：</td>
                <td>{{info.companyTypeKey|enumtext(companyType)}}</td>
                <td>开户银行：</td>
                <td>{{info.bankName}}</td>
                <td>行号：</td>
                <td>{{info.bankCode}}</td>
            </tr>
            <tr>
                <td>银行账号：</td>
                <td>{{info.bankAccount}}</td>
                <td>税号：</td>
                <td>{{info.taxNum}}</td>
                <td>开票地址：</td>
                <td>{{info.invoiceAddress}}</td>
            </tr>
            <tr>
                <td>办公地址：</td>
                <td colspan="5">{{info.officeAddress}}</td>
            </tr>
            <tr>
                <td>公司电话：</td>
                <td>{{info.companyTelphone}}</td>
                <td>公司传真：</td>
                <td>{{info.companyFax}}</td>
                <td>法人代表：</td>
                <td>{{info.legalPersonName}}</td>
            </tr>
            <tr>
                <td>法人代表电话：</td>
                <td>{{info.legalPersonMobile}}</td>
                <td>信誉度：</td>
                <td>{{info.creditDegree}}</td>
                <td></td>
                <td></td>
            </tr>
            </tbody>
        </table>
        <br/>
        <p class="dataManagement-title">联系人信息</p>
        <el-button  type="success" size="mini" style="float:right;margin-bottom:10px" @click="contactAdd">添加联系人</el-button>
        <el-form :model="form" ref="contactForm">
            <el-table :data="form.list" border>
                <el-table-column  label="姓名"  align="center" >
                    <template slot-scope="scope" :show-overflow-tooltip="true">
                        <el-form-item  :prop="'list.'+scope.$index+'.name'" :rules="[{ required: true, message: '请输入', trigger: 'blur' }]"  v-if="scope.$index==cIndex"><el-input  size="mini"  v-model="scope.row.name" ></el-input></el-form-item>
                        <span v-else>{{scope.row.name}}</span>
                    </template>
                </el-table-column>
                <el-table-column  label="电话"  align="center" :show-overflow-tooltip="true">
                    <template slot-scope="scope">
                        <el-form-item  :prop="'list.'+scope.$index+'.telphone'" :rules="[{ required: true, message: '请输入', trigger: 'blur' }]"  v-if="scope.$index==cIndex"><el-input  size="mini"  v-model="scope.row.telphone" ></el-input></el-form-item>
                        <span v-else>{{scope.row.telphone}}</span>
                    </template>
                </el-table-column>
                <el-table-column  label="手机"  align="center" :show-overflow-tooltip="true">
                    <template slot-scope="scope">
                        <el-input  size="mini"  v-model="scope.row.mobile" v-if="scope.$index==cIndex"></el-input>
                        <span v-else>{{scope.row.mobile}}</span>
                    </template>
                </el-table-column>
                <el-table-column  label="邮箱"  align="center" :show-overflow-tooltip="true">
                    <template slot-scope="scope">
                        <el-input  size="mini"  v-model="scope.row.email" v-if="scope.$index==cIndex"></el-input>
                        <span v-else>{{scope.row.email}}</span>
                    </template>
                </el-table-column>
                <el-table-column  label="职位"  align="center" :show-overflow-tooltip="true">
                    <template slot-scope="scope">
                        <el-form-item  :prop="'list.'+scope.$index+'.position'" :rules="[{ required: true, message: '请输入', trigger: 'blur' }]"  v-if="scope.$index==cIndex"><el-input  size="mini"  v-model="scope.row.position"></el-input></el-form-item>
                        <span v-else>{{scope.row.position}}</span>
                    </template>
                </el-table-column>
                <el-table-column  label="邮编"  align="center" :show-overflow-tooltip="true">
                    <template slot-scope="scope">
                        <el-input  size="mini"  v-model="scope.row.postcode" v-if="scope.$index==cIndex"></el-input>
                        <span v-else>{{scope.row.postcode}}</span>
                    </template>
                </el-table-column>
                <el-table-column  label="地址"  align="center" :show-overflow-tooltip="true">
                    <template slot-scope="scope">
                        <el-input  size="mini"  v-model="scope.row.address" v-if="scope.$index==cIndex"></el-input>
                        <span v-else>{{scope.row.address}}</span>
                    </template>
                </el-table-column>
                <el-table-column  label="操作"  align="center" >
                    <template slot-scope="sp">
                        <el-button-group v-if="sp.$index==cIndex">
                            <el-button  size="mini" @click="cancel(sp.$index)" icon="el-icon-error" ></el-button>
                            <el-button  size="mini" @click="confirm(sp.row)" icon="el-icon-success" type="primary" :disabled="uploading"></el-button>
                        </el-button-group>
                        <el-button-group v-else>
                            <el-button type="primary" icon="el-icon-edit" size="mini" @click="edit(sp.$index)"></el-button>
                            <el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteCon(sp.row)"></el-button>
                        </el-button-group>
                    </template>
                </el-table-column>
            </el-table>
        </el-form>
    </div>
</template>
<script>
    export default {
        name:"SupplierInfo",
        mounted(){
            this.doAjax();
            this.getList()
        },
        data(){
            return{
                info:{},
                cIndex:-1,
                nod:true,//新增标识,
                form:{
                    list:[]
                },
                uploading:false
            }
        },
        methods:{
            doAjax(){
                this.$http.post('/ys-web-wms/supplier/getInfo',{id:this.$route.params.id}).then(
                    (res)=>{
                        if(res.data)
                            this.info=Object.assign({},res.data.data)
                        this.$store.commit('SET_CURRENTROW',this.info)
                    }
                )
            },
            getList(){
                this.$http.post('/ys-web-wms/supplier/contact/getList',{id:this.$route.params.id}).then(
                    (res)=>{
                        if(res.data)
                            this.form.list=res.data.data?res.data.data:[]
                        this.resetNod()
                    }
                )
            },
            contactAdd(){
                if(this.nod){
                    this.form.list.push({
                        name:'',
                        telphone:'',
                        mobile:'',
                        email:'',
                        position:'',
                        postcode:'',
                        address:''
                    })
                    this.cIndex = this.form.list.length-1
                }else{
                    this.form.list.splice(this.form.list.length-1,1)
                    this.cIndex = -1
                }
                this.nod =  !this.nod
            },
            edit(val){
                this.cIndex = val
            },
            confirm(val){

                let url = "add"
                let id = this.$route.params.id
                if(this.nod){
                    url = "edit"
                    id = val.contactsId
                }
                this.$refs['contactForm'].validate((valid)=>{
                    if(valid){
                        this.uploading = true
                        this.$http.post("/ys-web-wms/supplier/contact/"+url, {params: JSON.stringify(val),id:id})
                            .then((response) => {
                                if (response.data.data.status == '200') {
                                    this.$message({
                                        'type': 'success',
                                        message: "操作成功",
                                        'showClose': true
                                    });
                                    this.getList()
                                    this.uploading = false
                                } else {
                                    this.$message({
                                        'type': 'error',
                                        message: "操作失败，,请重试或联系管理",
                                        'showClose': true
                                    });
                                    this.uploading = false
                                }
                            })
                            .catch((error) => {
                                console.log(error)
                                this.uploading = false
                            });
                    }else{
                        this.$nextTick(()=> {
                            document.getElementsByClassName("el-form-item__error")[0].parentNode.getElementsByClassName('el-input__inner')[0].scrollIntoView()
                            document.getElementsByClassName("el-form-item__error")[0].parentNode.getElementsByClassName('el-input__inner')[0].focus()
                        })
                    }
                })
            },
            cancel(val){
                if(!this.nod){
                this.form.list.splice(val,1)
                }
                this.resetNod()
            },
            deleteCon(val){
                this.$confirm('确认删除该项?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$http.post("/ys-web-wms/supplier/contact/delete", {id:val.contactsId})
                        .then((response) => {
                            if (response.data.data.status == '200') {
                                this.$message({
                                    'type': 'success',
                                    message: "操作成功",
                                    'showClose': true
                                });
                                this.getList()
                            } else {
                                this.$message({
                                    'type': 'error',
                                    message: "操作失败，,请重试或联系管理",
                                    'showClose': true
                                });
                            }
                        })
                        .catch((error) => {
                            console.log(error)
                        });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            resetNod(){
                this.nod = true
                this.cIndex = -1
            }
        },
        watch:{
            '$route.params.id'(){
                this.doAjax()
                this.getList()
            }
        },
        computed:{
            YesNo(){
                return this.$store.state.enumList.YesNo
            },
            companyType(){
                return this.$store.state.enumList.companyType
            }
        }
    }
</script>
<style scoped>
    .info-table td:nth-child(odd){
        width:100px;
    }
</style>



